@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;1,300;1,400;1,500;1,600&display=swap);
*{box-sizing:border-box}html{font-size:10px}body{margin:0}a{color:inherit;text-decoration:none}.app{width:108rem;margin:0 auto}

.header{display:flex;align-items:center;justify-content:space-between;min-height:73px;padding:7px 6rem;background-color:#009DE9;font-family:Open Sans;font-style:normal;font-weight:normal;font-size:3rem;line-height:4.1rem;letter-spacing:0.02em;color:#ffffff}.header__weather{position:relative;margin-left:5rem}.header__image{position:absolute;top:-20px}

.slider{position:relative;min-height:608px}.slider__btn{position:absolute;bottom:-35px;width:71px;height:71px;border:none;border-radius:50%;outline:none;background-position:center !important;background-repeat:no-repeat !important;background-color:rgba(0,0,0,0.14) !important;cursor:pointer;color:transparent !important}.slider__btn_left{background-image:url(data:image/png;base64,) !important;left:20px}.slider__btn_right{background-image:url(data:image/png;base64,) !important;right:20px}

.main{min-height:1129px;padding:2.5rem 2rem 6rem;background:linear-gradient(115.6deg, #3EB5F1 3.7%, #4E6FB3 59.81%, #3EB5F1 103.65%)}.main__cards-container{display:flex;flex-wrap:wrap;justify-content:center;align-content:flex-start;padding-left:3.5rem}.main__title{position:relative;font-family:Open Sans;font-style:normal;font-weight:bold;font-size:2.6rem;line-height:3.5rem;text-align:center;text-transform:uppercase;color:#2C2C33}.main_section{background:url(/static/media/back.397fffdc.png)}.main__btn{position:absolute;background-color:transparent;border:none;outline:none;cursor:pointer;top:-15px}.main__btn_back{right:-7px}.main__btn_home{left:-5px}.main__btn:hover{-webkit-filter:drop-shadow(2px 6px 10px rgba(31,29,113,0.3));filter:drop-shadow(2px 6px 10px rgba(31,29,113,0.3))}.main__text{width:82%;margin:3rem auto;font-family:Roboto;font-style:normal;font-weight:normal;font-size:2rem;line-height:2.3rem;letter-spacing:0.02em;color:#2C2C33}

.card{display:flex;flex-direction:column;justify-content:space-between;height:25rem;width:30rem;padding:6rem 2rem 4rem;border-radius:4.5rem;margin-top:3.5rem;margin-right:3.5rem;background:#FFFFFF;box-shadow:2px 11px 16px rgba(100,105,108,0.24);font-family:'Roboto', sans-serif;font-weight:500;font-size:2.2rem;line-height:2.6rem;text-align:right;letter-spacing:0.02em;text-transform:uppercase;color:#2B2E2D}.card__link{width:8rem;height:7.8rem}.card__link:hover{-webkit-filter:drop-shadow(0px 4px 4px rgba(63,132,206,0.6));filter:drop-shadow(0px 4px 4px rgba(63,132,206,0.6));cursor:pointer}

.footer{position:relative;display:flex;flex-direction:column;justify-content:space-between;min-height:110px;padding:14px 6rem 8px;background-color:#009DE9;font-family:'Montserrat', sans-serif;letter-spacing:0.02em;color:#ffffff}.footer__row{display:flex;flex-wrap:wrap;align-items:center;justify-content:center}.footer__row_post{font-size:1.6rem;font-weight:500;line-height:2rem}.footer__row_post .footer__text:first-child{width:31rem;text-align:end}.footer__row_post .footer__text:last-child{width:28rem}.footer__row_post .footer__logo{margin:0 4.5rem}.footer__row_tehno{font-size:1.4rem;font-weight:600;line-height:1.7rem}.footer__row_tehno .footer__logo{width:75px;height:17px;object-fit:contain;margin:0 1.9rem}.footer__button{width:10rem;height:3rem;position:absolute;right:3rem;top:6rem;border-radius:1.5rem;cursor:pointer;border:none}

