.footer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 110px;
  padding: 14px 6rem 8px;
  background-color: #009DE9;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 0.02em;
  color: #ffffff;
  &__row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    &_post {
      font-size: 1.6rem;
      font-weight: 500;
      line-height: 2rem;
      .footer__text {
        &:first-child {
          width: 31rem;
          text-align: end;
        }
        &:last-child {
          width: 28rem;
        }
      }
      .footer__logo {
        margin: 0 4.5rem;
      }
    }
    &_tehno {
      font-size: 1.4rem;
      font-weight: 600;
      line-height: 1.7rem;

      .footer__logo {
        width: 75px;
        height: 17px;
        object-fit: contain;
        margin: 0 1.9rem;
      }
    }
  }
  &__button {
    width: 10rem;
    height: 3rem;
    position: absolute;
    right: 3rem;
    top: 6rem;
    border-radius: 1.5rem;
    cursor: pointer;
    border: none;
  }
}